@import url('https://fonts.googleapis.com/css2?family=Jost:wght@400;500;600;700;900&family=Montserrat:wght@300;600&family=Poppins:wght@300;400;500;600;700&display=swap');

* {
  margin: 0;
  padding: 0;
}
body {
  background-color: #fff;
  color: #222;
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
}
.wrapper{
  width: 1280px;
  margin: 50px auto;
}
header{
  position: relative;
}
header .logo{
font-weight: 600;
font-size: 20px;
}
header ul.nav{
  float: right;
  list-style: none;
}
header ul.nav li{
  display: inline;
  margin-left: 20px;
  cursor: pointer;
  transition: opacity 500ms ease;
}
header ul.nav li:hover{
  opacity: 50%; 
}
header .presentation{
  position: relative;
  margin: 50px 0;
  background: url("./img/head-foto.jpg") no-repeat;
  width: 100%;
  height:500px;
  background-size: cover;
  background-position: center center;
  background-blend-mode: multiply;
  background-color: #bcbcbc;
  
}
header .presentation::after{
  position: absolute;
  content: "Beste Waren fÃ¼r Ihr Haus";
  top: 50px;
  left: 50px;
  width: 300px;
  font-size: 40px;
  font-weight: 600;
  color: #fff;
}

header .presentation::before{
  position: absolute;
  content: "Niedrige Preise";
  top: 150px;
  left: 50px;
  width: 300px;
  font-size: 15px;
  font-weight: 300;
  color: #fff;
}
header .shop-cart-button{
  float: right;
  cursor: pointer;
  transition: color, transform 500ms ease;
}
header .shop-cart-button:hover, header .shop-cart-button:active
{
  color: #dc3d3d;
  transform: scale(1.3);
}
header .shop-cart{
  position: absolute;
  top: 30px;
  right: 0;
  width: 450px;
  padding: 20px;
  background: #f5f5f5 ;
  box-shadow: 5px 4px 12px 0px rgba(185,174,174,0.75);
  -webkit-box-shadow: 5px 4px 12px 0px rgba(185,174,174,0.75);
  -moz-box-shadow: 5px 4px 12px 0px rgba(185,174,174,0.75);
  z-index: 1000;
}
header .shop-cart .item{
  width:100%;
  float: left;
  margin: 20px;
  margin-bottom: 0;

}
header .shop-cart .item img{
  width: 70px;
  float: left;
  margin-right: 20px;
}
header .shop-cart .item h2{
  font-size: 20px;
  margin-bottom: 10px;
}
header .shop-cart .item b {
  color: #797979;
  font-weight: 600;
}
header .shop-cart .empty h2{
  font-size: 15px;
  margin-bottom: 15px;
}
header .shop-cart .item .delete-icon{
  color: #ca5252;
  float:right;
  position: relative;
  top: -25px;
  right: 15px;
  cursor: pointer;
  transition: color, transform 500ms ease;
}
header .shop-cart .item .delete-icon:hover{
  color: #d83030;
  transform: scale(1.3);
}
header .shop-cart .summa{
  float:left;
  width: 100%;
  font-size: 15px;
  font-weight: 600;
  margin: 20px;
  margin-bottom: 10px;
}
.categories div{
display: inline-block;
background: #f2f2f2;
border-radius: 50px;
padding: 10px 20px;
margin-bottom: 25px;
margin-right: 15px;
cursor: pointer;
border: 1px solid transparent;
transition: all 500ms ease;
}
.categories div:hover{
  border-color: silver;
  transform: scale(1.1);
}
main{
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-between;
}
main .item{
  position: relative;
  width: 30%;
  margin-bottom: 50px;
  background-color: #f5f5f5;
  overflow: hidden;
  padding-bottom: 20px;
}
main .item img,
.full-item img{
  width: 100%;
  border-radius: 10px 10px 0 0;
  transition: transform 500ms ease;
  cursor: pointer;
}
main .item img:hover,
.full-item img:hover{
transform: scale(1.05);
}

main h2, .full-item h2,
 main p, .full-item p,
  main b, .full-item b{
  margin: 10px 20ps;
  color: #333;
}
main b, .full-item b {
  color: #5fa36a;
}
main .add-to-cart,
.full-item .add-to-cart {
  position: absolute;
  right: 20px;
  bottom: 20px;
  background: #ca5252;
  width: 35px;
  height: 35px;
  text-align: center;
  line-height: 35px;
  color: #fff;
  border-radius: 50%;
  cursor: pointer;
  font-weight: 600;
  transition: transform 500ms ease;
}
main .add-to-cart:hover,
.full-item .add-to-cart:hover{
  transform: scale(1.3) translateY(-5px);
}
.full-item {
  position: fixed;
  top:0;
  bottom:0;
  right:0;
  left:0;
  background: #2c2b2b;
  z-index: 9999;
  overflow: auto;
}
.full-item > div{
  width:600px;
  position: relative;
  margin: 10% auto;
  padding: 40px 30px;
  background: #fff;
  border-radius: 9px;
}
footer{
  text-align: center;
  margin-top: 100px;
}

@media screen and (max-width: 1500px) {
  .wrapper{
    width:100%;
    padding: 10px;
  }
  main{
    flex-direction: row;
    justify-content: center;
  }
  main .item{
   width: 40%;
   align-items: center;
   background-color: #fff;
}
  main .item img{
    width:80%;
  }
  header .presentation{
    margin: 20px 0;
}
}
@media screen and (max-width: 800px) {

  main .item{
    width: 100%;
    padding: 15%;
    margin-bottom: 10px;
}
.categories div{
  margin-bottom: 0px;
}
header ul.nav li{
  display: inline-block;
  margin-left: 10px;
  margin-bottom: 10px;
}
}
